<script>
export default {
  data() {
    return {
      //return(component)
      allLed: true,
      isEnable: false,
      isDisable: false,
      time: 5
    };
  },
  methods: {
    triggerLed() {
      this.allLed = true;
      this.$socket.emit('ledOnEvent', this.allLed);
      this.isEnable = true;
    },
    closeLed() {
      this.allLed = false;
      this.$socket.emit('ledOffEvent', this.allLed);
      this.isDisable = true;
    }
  },
  watch: {
    isDisable() {
      if (!this.isDisable) {
        return; //(:throttle)
      }
      const vm = this;
      let timer = null;
      timer = setInterval(() => {
        vm.time--;
        if (this.time === 0) {
          clearInterval(timer);
          vm.time = 5;
          vm.isEnable = false;
          vm.isDisable = false;
        }
      }, 1000);
    }
  }
};
</script>

<template>
  <div class="container">
    <h1>Traffic-Control-System</h1>
    <button
      :class="['btn btn-primary', { collapsed: !isEnable }]"
      @click="triggerLed"
      :disabled="isEnable"
      type="button"
      data-toggle="collapse"
      data-target="#collapseExample"
      aria-expanded="false"
      aria-controls="collapseExample"
    >
      
    </button>
    <button
      :class="['btn btn-danger', { collapsed: !isDisable }]"
      @click="closeLed"
      :disabled="isDisable"
      type="button"
      data-toggle="collapse"
      data-target="#collapseExample"
      aria-expanded="false"
      aria-controls="collapseExample"
    >
      
    </button>

    <div :class="['collapse', { show: isDisable }]" id="collapseExample">
      <div class="card card-body">
        <span>{{ time }}</span>
      </div>
    </div>
  </div>
</template>
