<script>
import { Bar } from 'vue-chartjs'
export default {
  extends: Bar,
  data() {
    return {
      loaded: false,
      sensordata: {
        labels: [''], //x(key)
        datasets: [
          {
            label: '', //x
            data: [], //y()
            backgroundColor: ['rgba(54, 162, 235, 0.2)'],
            borderWidth: 1, // 
          },
        ],
      },
      options: {
        responsive: true,
        maintainAspectRatio: false,
        scales: {
          yAxes: [
            {
              ticks: {
                min: 0,
                max: 20,
                stepSize: 1, // y
              },
            },
          ],
        },
      },
    }
  },
  mounted() {
    this.renderForceSensitiveResistorChart()
  },
  methods: {
    renderForceSensitiveResistorChart() {
      this.sockets.subscribe('ForceSensitiveResistorEvent', (data) => {
        this.$data._chart.config.data.datasets[0].data.splice(0, 1, data) //replaces 1 element at index 4
        this.renderForceSensitiveResistorChart()
        console.log(data)
        console.log(this)
        console.log(this.$data._chart.config.data.datasets[0].data)
        //this.sensordata.datasets[0].data = [data, 1]
      })
      this.renderChart(this.sensordata, this.options)
    },
  },
  watch: {
    sensordata() {
      this.$data._chart.destroy()
      this.renderForceSensitiveResistorChart()
    },
    deep: true,
  },
}
</script>
